import {GETDATA, ADD, EDIT, DELETE} from './actionTypes';
// 
import axios from 'axios';
const API_KEY = '5d10b2aff8e638840eebed810d5ec4bc';
const ROOT_URL = `http://api.openweathermap.org/data/2.5/forecast?appid=${API_KEY}`;
// 

export function getData(city) {
	const url = `${ROOT_URL}&q=redwood city,us`;
	const request = axios.get(url);

	return {
		type: GETDATA,	// action type
		payload: request		// additional data
	};
}

export const add = (item) => ({
  type: ADD,
  payload: item
});

export const edit = (text) => ({
  type: EDIT,
  payload: { text }
});


export const remove = (text) => ({
  type: DELETE,
  payload: { text }
});


