import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';

export default class Navigation extends Component {

    
    componentWillMount() {
    this.props = {links: [
        // {title:'Home', href: '/'},
        {id: '1', title:'About', href: '/about'},
        {id: '2', title:'Sign In', href: '/signin'},
        {id: '3', title:'Orders', href: '/orders'},
        ]};
        this.setState({ 
            links: this.props.links
        })
    }

        renderLinks() {
            var {links} = this.state;

            if (this.props.authenticated ) {
              // show a link to sign out
              return <li><Link key={'1'} className="white-text" to="/signin">Sign In</Link></li>
            } else if(links) {
              // show a link to sign in or sign up
              return links.map(function(item) {
                      return (
                        <li key={item.id}><Link className="white-text" to={item.href}>{item.title}</Link></li>
                      );
                    });
            }
        }


	render() {
		return (
			<nav className="teal" role="navigation">
                <div className="nav-wrapper container">
                    <Link id="logo-container" to="#" className="brand-logo white-text">dddsss</Link>
                    <ul id="nav-desktop" className="right hide-on-med-and-down">
                       {this.renderLinks()}
                    </ul>

                    <ul id="nav-mobile" className="side-nav">
                        {this.renderLinks()}
                    </ul>
                    <a href="#" data-activates="nav-mobile" className="button-collapse"><i className="material-icons">menu</i></a>
                </div>
            </nav>
		);
	}
}



