import _ from 'lodash';
import React       from 'react';
import { reduxForm } from 'redux-form';
import * as actions from './actions';

const FIELDS = {
  title: { label: 'Title' },
  header: { label: 'header' },
  subheader: { label: 'subheader' },
  checkbox: { label: 'checkbox' ,type: 'checkbox' }
};

const validate = values => {
  const errors = {}
  if (!values.title) {
    errors.title = 'Required'
  } else if (values.title.length > 15) {
    errors.title = 'Must be 15 characters or less'
  }
console.log('validate', values)
  return errors
}


const submit = (values, dispatch) => {
  console.log('test', values)
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      if (true) {
        reject({ password: 'Wrong password', _error: 'Login failed!' })
      } else {

        dispatch(showResults(values))
        resolve()
      }
    }, 1000) // simulate server latency
  })
}
const buildField = (props, field, fieldName) => {
  const fieldProps = props.fields[fieldName];
  const error = fieldProps.error && fieldProps.touched ?'invalid':'';
  if('checkbox'== field.type){
     return (
      <p key={field.label}>
        <input type="checkbox" id={field.label} />
        <label htmlFor={field.label} >{field.label}</label>
         {field.touched && field.error && <div>{field.error}</div>}
      </p>
      );
    } else {
      if(fieldProps.error){
        console.log('fieldProps',fieldProps)
      }
  return (
   
    <div key={field.label} className="input-field">
      <input  placeholder={field.label} id={field.label} type="text" className="{'validate' + error}" {...fieldProps} />
      <label htmlFor={field.label}></label>
       
    </div>
  
  );
}
}

const PostNew = (props) => {
  const { fields: { title, body }, error, resetForm, submitting, handleSubmit } = props;
  return (
    <div className="row">
      <form onSubmit={handleSubmit(submit)}>
        {_.map(FIELDS, buildField.bind(null, props))}
        {error && <div>{error}</div>}
        <button  className="btn waves-effect waves-light right" action="submit" disabled={submitting} >Submit</button>
        <button className="btn btnwaves-effect waves-light right" type="button" disabled={submitting} onClick={resetForm}> Clear Values </button>
      </form>
    </div>
  );
};

export default reduxForm({
  form: 'PostNew',
  fields: _.keys(FIELDS),
  validate
}, null, actions)(PostNew);


