import {GETDATA, ADD, EDIT, DELETE} from './actionTypes';

const initialState = [{
  list: [],
  text: 'Orders List',
  completed: false,
  id: 0
}];

export default function(state = initialState, action) {
  switch (action.type) {
    case GETDATA:
      return { ...state, list:  action.payload.data.list };
    case ADD:
      return { ...state, list: [...state.list, action.payload] };
    case DELETE:
      return { ...state, list: []};
    case EDIT:
      return { ...state, list: []};
  }

  return state;
};


